<script>
import ProfilePicture from "@/components/ProfilePicture.vue";
import Bubble from "@/components/chat/Bubble.vue";
import ChatBox from "@/components/chat/chatBox.vue";
import Button from "@/components/button.vue";
import SearchInput from "@/components/searchInput.vue";
export default {
  name: "Messages2",
  components: {SearchInput, Button, ChatBox, Bubble, ProfilePicture},
  data() {
    return {
      heads: ['نام و نام خانوادگی', 'یوزرنیم', 'سوال کاربر', 'جواب ادمین', 'حذف', 'بستن'],
      datum: [],
      messagesDatum: [],
      profile:null,
      sendMessageParam: {
        text: '',
        image: null
      },
      chatbox: false,
      Socket: null,
      isMobile: false,
      isOpen: false,
    }
  },
  computed: {
    isModalVisible() {
      return this.isOpen;
    }
  },
  methods:{
    onToggle() {
      this.isOpen = !this.isOpen;
    },
    close(val){
      this.onToggle()
      console.log('chatbox closed.', val)
    },
    handleMediaChange(event) {
      this.isMobile = event.matches;
      console.log('isMobile: ', this.isMobile);
    },
    loadMessagesContents(){
      this.axios.get('/api/loadMessagesContents').then((response) => {
        this.datum = response.data['data']
        this.copyOfDatum = response.data['data']
      })
    },
    loadMessagesyHistoryOfProfile(row){
      this.profile = row
      console.log('profile is: \n', this.profile)
      const params = {
        "id": row['id'],
      };
      this.axios.get('/api/loadMessagesyHistoryOfProfile',{params: params}).then((response) => {
        this.messagesDatum = response.data['data'];
        console.log('messages loaded...\n', this.messagesDatum);
        this.chatbox = true;
      })
      this.$emit('closeChatbox', false);
      if (this.isMobile) {
          this.onToggle()
      }
    },
    modalHandlerMessages(event){
      if (event==true){
      }
    },
    filterBasedOnColumn(event) {
      console.log(event)
      let filter = event['filter']
      let searchTerm = event['input']
      if (searchTerm == ''){
        this.datum = this.copyOfDatum
      } else {
        this.datum = this.datum.filter(item => {
          const searchText = searchTerm.toLowerCase();
          // Search fullName using includes
          if (filter == 'نام و نام خانوادگی'){
            return item.enter_name.toLowerCase().includes(searchText);
          }
          if (filter == 'یوزرنیم'){
            return item.enter_id.toLowerCase().includes(searchText);
          }
        });
      }
    },
  },
  mounted() {
    this.loadMessagesContents();

    // Initialize WebSocket connection
    const wsProtocol = window.location.protocol === 'https:' ? 'wss://' : 'ws://';
    const socketUrl = `${wsProtocol}${window.location.host}`;

    this.Socket = new WebSocket(socketUrl + '/ws/supportMessages/');
    this.Socket.onopen = function(event) {
      console.log('WebSocket connection opened');
    };
    // Set up message handler
    this.Socket.onmessage = (e) => {
      this.loadMessagesContents();
    };

    // Handle WebSocket errors
    this.Socket.onerror = (error) => {
      console.error('WebSocket Error: ', error);
    };

    // Match Tailwind's 'sm' breakpoint (640px)
    const mediaQuery = window.matchMedia('(max-width: 639px)');

    // Initial check
    this.handleMediaChange(mediaQuery);

    // Listen for changes
    mediaQuery.addListener(this.handleMediaChange);

  },
  beforeDestroy() {
    // Close WebSocket connection when component is destroyed
    if (this.Socket) {
      this.Socket.close();
    }

        // Cleanup listener
    const mediaQuery = window.matchMedia('(max-width: 639px)');
    mediaQuery.removeListener(this.handleMediaChange);
  },
}
</script>

<template>
<div class="flex justify-between h-[calc(100vh-1rem)] bg-white">
  <div class="w-full overflow-auto">
    <nav class="bg-white shadow container mx-auto">
        <div class="max-w-screen-xl flex flex-wrap items-center justify-between mx-auto p-4 gap-2">
          <a href="#" class="flex items-center space-x-3 rtl:space-x-reverse">
            <img src="@/assets/friends.png" class="h-8" alt="آیکن مهمون شو" />
            <span class="self-center text-2xl font-semibold whitespace-nowrap dark:text-white">مهمونشو</span>
          </a>
          <button data-collapse-toggle="navbar-solid-bg" type="button" class="inline-flex items-center p-2 w-10 h-10 justify-center text-sm text-gray-500 rounded-lg md:hidden hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-gray-200 dark:text-gray-400 dark:hover:bg-gray-700 dark:focus:ring-gray-600" aria-controls="navbar-solid-bg" aria-expanded="false">
            <span class="sr-only">Open main menu</span>
            <svg class="w-5 h-5" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 17 14">
              <path stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M1 1h15M1 7h15M1 13h15"/>
            </svg>
          </button>
          <div class="hidden w-full md:block md:w-auto " id="navbar-solid-bg">
            <ul class="flex flex-col font-medium mt-4 rounded-lg bg-gray-50 md:space-x-8  md:items-center gap-2 rtl:space-x-reverse md:flex-row md:mt-0 md:border-0 md:bg-transparent dark:bg-gray-800 md:dark:bg-transparent dark:border-gray-700">
              <li>
                <button type="button" class="truncate font-sans flex flex-row items-center justify-between gap-2 px-5 py-2.5 text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:ring-blue-300 font-medium rounded-lg text-md"
                 @click="$emit('sidebarOpen', false)">
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-menu-button" viewBox="0 0 16 16">
                      <path d="M0 1.5A1.5 1.5 0 0 1 1.5 0h8A1.5 1.5 0 0 1 11 1.5v2A1.5 1.5 0 0 1 9.5 5h-8A1.5 1.5 0 0 1 0 3.5zM1.5 1a.5.5 0 0 0-.5.5v2a.5.5 0 0 0 .5.5h8a.5.5 0 0 0 .5-.5v-2a.5.5 0 0 0-.5-.5z"/>
                      <path d="m7.823 2.823-.396-.396A.25.25 0 0 1 7.604 2h.792a.25.25 0 0 1 .177.427l-.396.396a.25.25 0 0 1-.354 0M0 8a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v5a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2zm1 3v2a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1v-2zm14-1V8a1 1 0 0 0-1-1H2a1 1 0 0 0-1 1v2zM2 8.5a.5.5 0 0 1 .5-.5h9a.5.5 0 0 1 0 1h-9a.5.5 0 0 1-.5-.5m0 4a.5.5 0 0 1 .5-.5h6a.5.5 0 0 1 0 1h-6a.5.5 0 0 1-.5-.5"/>
                    </svg>
                    <span class="sr-only">open menu</span>
                </button>
              </li>
              <li>
                <search-input @serachInput="filterBasedOnColumn" placeholder="جستوجو در کاربرها" :filterOptions="['نام و نام خانوادگی', 'یوزرنیم']"></search-input>
              </li>
            </ul>
          </div>
        </div>
      </nav>
<!--    contacts-->
    <ul class="divide-y divide-gray-200" v-for="row in datum">
       <li @click="loadMessagesyHistoryOfProfile(row)"
           class="pb-3 sm:pb-4 px-4 py-2 border-b border-gray-200 cursor-pointer hover:bg-gray-100 hover:text-blue-700">
          <div class="flex items-center space-x-4 rtl:space-x-reverse">
             <div class="flex-shrink-0">
               <ProfilePicture :username="row['enter_id']" :src="row['profile_picture']"></ProfilePicture>
             </div>
             <div class="flex-1 min-w-0 mx-auto">
                <p class="text-sm font-medium text-gray-900 truncate dark:text-white">
                  {{ row['enter_name'] }}
                </p>
                <p class="text-sm text-gray-500 truncate dark:text-gray-400 max-w-sm">
                  {{ row['last_message'] }}
                </p>
             </div>
             <div class="">{{ row['total_unread_messages'] }}</div>
          </div>
       </li>
    </ul>

  </div>
  <div class="bg-gray-200 w-full hidden sm:block overflow-auto">
    <chat-box
        :messages="messagesDatum"
        :profile="profile"
        @closeChatbox="chatbox=false"
        :isModal="false"
    />
  </div>
</div>

    <!-- Render inside our `<div id="modals"></div>` in index.html -->
    <Teleport to="body">
      <transition name="fade">
      <!-- Show / hide the modal -->
      <div :class="[isModalVisible? '': 'hidden']">
        <!-- The backdrop -->
        <div @click="onToggle" class="fixed inset-0 z-50 bg-gray-900 opacity-40"></div>

        <!-- Where the actual content goes -->
        <div class="fixed inset-0 flex items-center z-50 justify-center">

          <!-- Modal body -->
            <chat-box
                :messages="messagesDatum"
                :profile="profile"
                @closeChatbox="close($event)"
                class="mx-2 px-2 max-h-[600px]"
                :isModal="true"
            />
        </div>
      </div>
      </transition>
    </Teleport>

</template>
